.default-layout {
  display: flex;
  flex-direction: column;

  .default-header {
    display: flex;
    flex-basis: 40px;
    padding: 10px 20px;
    background-color: #1890ff;
    position: sticky;
    top: 0;
    z-index: 10;

    .search {
      width: 300px;
    }
  }

  .default-content-layout {
    display: flex;
    padding: 30px;

    .default-sidebar {
      flex-basis: 250px;
    }

    .default-content {
      width: 100%;
      margin-left: 20px;
    }
  }
}
